
.song-menu {
    height: 30vh;
    margin: 10px;
    border-radius: 10px;
    background-size: 100%;
    background-position: bottom;
    filter: blur(0.3px);
    box-shadow: 0 0 10px rgba(0,0,0,0.6);
    align-items: center;
    justify-content: center;
}

.equalizer-div {
    display: relative;
    width: 24px;
    margin: 14vh auto;
}

.equalizer{
    display:inline-block;
    margin: 0 auto;
    width:15px;
    background-color: transparent;
    border: 0;
    padding:0.5em;
    position: relative;
    transition: opacity 0.5s ease;
  }
  
  .equalizer:hover, .equalizer:focus{
    outline: 0;
    opacity: 0.5;
  }
  
  .equalizer span{
    display: inline-block;
    width: 3px;
    background-color: #ddd;
    position: absolute;
    bottom: 0;
  }
  
  @keyframes shorteq{
    0% {height: 10px}
    50% {height: 5px}
    100% {height: 10px}
  }
  @-webkit-keyframes shorteq{
    0% {height: 10px}
    50% {height: 5px}
    100% {height: 10px}
  }
  
  @keyframes talleq{
    0% {height: 15px}
    50% {height: 8px}
    100% {height: 15px}
  }
  @-webkit-keyframes talleq{
    0% {height: 15px}
    50% {height: 5px}
    100% {height: 15px}
  }
  
  .eq1{
    height: 13px;
    left: 0;
    animation-name: shorteq;
    animation-duration: 0.5s;
    animation-iteration-count: infinite;
    animation-delay: 0s;
    -webkit-animation-name: shorteq;
    -webkit-animation-duration: 0.5s;
    -webkit-animation-iteration-count: infinite;
    animation-delay: 0s;
  }
  
  .eq2{
    height: 15px;
    left: 6px;
    animation-name: talleq;
    animation-duration: 0.5s;
    animation-iteration-count: infinite;
    animation-delay: 0.17s;
    -webkit-animation-name: talleq;
    -webkit-animation-duration: 0.5s;
    -webkit-animation-iteration-count: infinite;
    animation-delay: 0.17s;
  }
  
  .eq3{
    height: 13px;
    left: 12px;
    animation-name: shorteq;
    animation-duration: 0.5s;
    animation-iteration-count: infinite;
    animation-delay: 0.34s;
    -webkit-animation-name: shorteq;
    -webkit-animation-duration: 0.5s;
    -webkit-animation-iteration-count: infinite;
    animation-delay: 0.34s;
  }
  .eq4{
    height: 13px;
    left: 18px;
    animation-name: talleq;
    animation-duration: 0.5s;
    animation-iteration-count: infinite;
    animation-delay: 0.34s;
    -webkit-animation-name: talleq;
    -webkit-animation-duration: 0.5s;
    -webkit-animation-iteration-count: infinite;
    animation-delay: 0.34s;
  }
  .eq5{
    height: 13px;
    left: 24px;
    animation-name: shorteq;
    animation-duration: 0.5s;
    animation-iteration-count: infinite;
    animation-delay: 0.34s;
    -webkit-animation-name: shorteq;
    -webkit-animation-duration: 0.5s;
    -webkit-animation-iteration-count: infinite;
    animation-delay: 0.34s;
  }
  