.audio-fles {
    display: flex;
    flex-wrap: wrap;
    padding: 5px;
    width: 100%;
    align-items: center;
    justify-content: center;
} 

.audio-fle {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    margin: 10px;
    box-shadow: 0 0 10px rgba(0,0,0,0.6)!important;
}

.audio-fle:active {
    opacity: 0.8;
}